<template>
  <div id="rate">
    <h3>基础用法</h3>
    <el-row type="flex" class="row-bg rate-box" justify="space-around">
      <el-col :span="12">
        <div class="block">
          <span class="demonstration">默认不区分颜色</span>
          <el-rate v-model="value1"></el-rate>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="block">
          <span class="demonstration">区分颜色</span>
          <el-rate v-model="value2" :colors="['#99A9BF', '#F7BA2A', '#FF9900']">
          </el-rate>
        </div>
      </el-col>
    </el-row>
    <h3>辅助文字
      <i>用辅助文字直接地表达对应分数</i>
    </h3>
    <el-row type="flex" class="row-bg rate-box" justify="space-around">
      <el-col :span="24">
        <el-rate v-model="value3" show-text>
        </el-rate>
      </el-col>
    </el-row>
    <h3>其它 icon
      <i>当有多层评价时，可以用不同类型的 icon 区分评分层级</i>
    </h3>
    <el-row type="flex" class="row-bg rate-box" justify="space-around">
      <el-col :span="24">
        <el-rate v-model="value4" :icon-classes="['el-icon-erp-wechat', 'el-icon-erp-icongithub', 'el-icon-erp-71']" void-icon-class="el-icon-erp-qq"
          :colors="['#99A9BF', '#F7BA2A', '#FF9900']">
        </el-rate>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        value1: null,
        value2: null,
        value3: null,
        value4: null
      }
    }
  }

</script>

<style lang="scss" scoped>
  .rate-box {
    padding: 4%;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-shadow: 3px 3px 8px #ccc;
  }

</style>



